import { GAMESTATE } from "../../GAME/state";

export class StartScreen extends HTMLElement {
    root: ShadowRoot;
    cssPath = `${GAMESTATE.paths.css}/startScreen/style.css`;
    globalElement!: HTMLElement;
    runGameButton: HTMLButtonElement;
    runCallback = () => { };

    constructor() {
        super();
        this.root = this.attachShadow({ mode: "open" });
        this.root.innerHTML = template(this.cssPath);
        this.runGameButton = this.root.querySelector(".run-game-button") as HTMLButtonElement;
        this.classList.add("screen");
    }
    connectedCallback() {
        this.globalElement = document.querySelector("bl-start-screen") as HTMLElement;
        this.runGameButton.addEventListener("click", () => {
            this.runCallback();
        })
    }
    setRunCallback(runCallback: () => void) {
        this.runCallback = runCallback
    }
    update() {

    }

    updateLanguage() {

    }
}
customElements.define("bl-start-screen", StartScreen);

function template(cssPath: string) {
    return `
    <link rel="stylesheet" type="text/css" href="${cssPath}"/>
    <div class="content">
        <h1>Start Screen</h1>
        <button class="run-game-button"></button>
    </div>
    `
}