import './index.css';
import "@babylonjs/loaders/glTF/2.0/glTFLoader";
import { InitGame } from './GAME/init';
import { createEnvironment } from './GAME/environment';
import { GAMEEVENTS, preload } from './GAME/Events/eventDefinition';
import "./GAME/Events/gameEvents"
import { GAMESTATE, screenManager } from './GAME/state';
import { SCREENS } from './GAME/types';
import { LevelScreen, StartScreen, PreloaderScreen, ResultScreen, EndgameScreen, LevelScreenMobile } from './UI/screensExport';
import { initAssets } from './GAME/Loaders/loaderModels';
import { Level } from './GAME/Level/level';
import { testPlatformWithBowser } from './testMobileDevice';


var root = document.querySelector("#root") as HTMLElement;
var canvas = document.createElement('canvas') as HTMLCanvasElement;
canvas.id = "game-canvas";
canvas.classList.add("screen");
GAMESTATE.state.canvas = canvas;
GAMESTATE.state.isMobile = testPlatformWithBowser();
(() => {
    root.append(
        screenManager.add(SCREENS.game, canvas),
        screenManager.add(SCREENS.level, new LevelScreen()),
        screenManager.add(SCREENS.levelMobile, new LevelScreenMobile()),
        screenManager.add(SCREENS.start, new StartScreen()),
        screenManager.add(SCREENS.preload, new PreloaderScreen()),
        screenManager.add(SCREENS.levelResult, new ResultScreen()),
        screenManager.add(SCREENS.endGame, new EndgameScreen()),
    );
})();


(async () => {
    var initData = await InitGame(canvas);
    for (let i = 0; i < 1; i += 1) {
        await preload();
    }
    if (initData) {
        var { scene, engine, havok } = initData;
        GAMESTATE.state.scene = scene;
        GAMESTATE.state.engine = engine;

        if (scene && engine && havok) {
            await initAssets(scene);
            (screenManager.getScreen(SCREENS.start) as StartScreen).setRunCallback(() => {
                dispatchEvent(new CustomEvent(GAMEEVENTS.runGame, { detail: { scene: scene } }));
            });
            GAMESTATE.level = Level();
            createEnvironment(scene);
            dispatchEvent(new CustomEvent(GAMEEVENTS.start, { detail: { scene: scene } }));
        }
    }
})();
