import { Mesh, PhysicsAggregate, Vector3 } from "../proxiBabylon";


export enum SCREENS {
    preload = 0,
    start = 1,
    game = 2,
    level = 3,
    levelMobile = 31,
    levelResult = 4,
    endGame = 5,
}
export enum GAMESTATUS {
    PRELOAD = 0,
    START = 1,
    RUN = 2,
    PAUSE = 3,
    LEVELEND = 4,
    END = 5,
}
export enum LEVELSTATE {
    RUN = 0,
    PAUSE = 1,
    STOP = 2,
    END = 3,
    GAMEEND = 4,
}
export enum MODELTYPE {
    EMPTY = 0,
    PLANE_0 = 1,
    SINGLE_HERO = 200,
    DOUBLE_HERO = 201,
}
export enum ENEMYMOVETYPE {
    LEFT = 0,
    RIGHT = 1,
    UP = 2,
    DOWN = 3,
    FWD = 4,
    STAB = 5,
    LOOP = 6
}
//TYPES
export type tBullet = {
    id: string,
    mesh: Mesh,
    physics: PhysicsAggregate,
    fire: (direction: Vector3) => void,
    dispose: () => void,
}
export type tEnemy = {
    id: string,
    mesh: Mesh,
    setPosition: (val: Vector3) => void,
    getPosition: () => Vector3,
    moveFWD: () => void;
    moveTo: (to: Vector3) => void,
    rotateY: (angle: number) => void,
    jump: (endCallback: () => void) => void,
    dispose: () => void,
    getState: () => any,
    attack: (frag: tEnemy) => boolean,
    damage: (val?: number) => string,
    rob: (enemy: tEnemy) => void, // обобрать мертвеца
    lookAt: (pt: Vector3) => void,
    lookAtEnemy: (enemy: tEnemy) => void,
    getRayData: () => any,
    nextTargetPt: () => void,
    toGround: () => void,
    getRadarCollider: () => Mesh,
    getRadarFragIntersection: () => void,
    escape: (fragPosition: Vector3, hunterId: string) => void,
    hunting: (enemy: tEnemy) => void,
}
export type tHero = {
    id: string,
    type: MODELTYPE,
    model: Mesh | null,
    base: Mesh | null
    aim: Mesh | null,
    stvol: Mesh | null,
    FOV: Mesh | null,
    setPosition: () => void,
}

export type tLevel = {
    id: number,
    state: () => boolean,
    run: () => void,
    stop: () => void,
}

export type tLevelDescription = {
    id: number,
    description: string,
    levelMap: Array<string>
}

//COLLISIONS DEFINE-------------------------------------------

export enum COLLIDE_GROUP {
    HERO = 0b00000001,
    ENEMY = 0b00000010,
    ENEMY_RADAR = 0b00000100,
    GROUND = 0b10000000,
    MARK = 0b01000000,

}
export enum COLLIDE_MASK {
    HERO = 0b10000000,
    ENEMY = 0b10000000,
    ENEMY_RADAR = 0b00000100,
    GROUND = 0b00000011,
    MARK = 0b00000000,

}