import { Vector3 } from "@babylonjs/core";

export function utils_RND_string(length: number = 10) {
    let result = '';
    const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    const charactersLength = characters.length;
    let counter = 0;
    while (counter < length) {
        result += characters.charAt(Math.floor(Math.random() * charactersLength));
        counter += 1;
    }
    return result;
}
export function utils_RND_float(): number {
    let rnd = 0;
    const seed = Math.random();
    if (seed < 0.2) rnd = 0.2;
    else if (seed > 0.4) rnd = 0.4;
    else rnd = seed;
    return rnd;
}
export function utils_RND_Clamp_integer(min: number, max: number) {
    const seed = Math.floor(Math.random() * max);
    if (seed > max) return max;
    if (seed < min) return min;
    return seed;
}
export function utils_signRandom() {
    return (Math.random() >= 0.5) ? 1 : -1;
}
export function utils_Vector3Random() {
    return new Vector3(
        utils_RND_Clamp_integer(80, 3) * utils_signRandom(),
        utils_RND_Clamp_integer(30, 10),
        -utils_RND_Clamp_integer(300, 250)
    )
}
export function utils_getNavigationPoints(points: Array<Vector3>, minDistance: number) {
    var pt1 = getRandomFromArray(points);
    var pt2 = getMinDistanceFromNavPoints(points, minDistance, pt1, pt1);
    // console.log(Vector3.DistanceSquared(pt1, pt2))

    return { pt1, pt2 }

}
export function utils_getDistancePoint(pt1: Vector3, points: Array<Vector3>, minDistance: number) {
    var pt2 = getMinDistanceFromNavPoints(points, minDistance, pt1, pt1);
    return pt2;
}
//--------------------------------------
var getMinDistanceFromNavPoints = (points: Array<Vector3>, distance: number, pt1: Vector3, pt2: Vector3) => {
    var bufPt = pt2;
    if (Vector3.DistanceSquared(pt1, pt2) < distance) {

        bufPt = getMinDistanceFromNavPoints(points, distance, pt1, getRandomFromArray(points))
    }
    return bufPt;
}
var getRandomFromArray = (arr: Array<any>) => arr[arr.length * Math.random() | 0];