import { GAMESTATE } from "../../GAME/state";

export class PreloaderScreen extends HTMLElement {
    root: ShadowRoot;
    contentPercent: HTMLElement;
    contentStage: HTMLElement;
    percent = 10;
    stage = "model";
    cssPath = `${GAMESTATE.paths.css}/preloaderScreen/style.css`;
    globalElement!: HTMLElement
    loaderImage: HTMLImageElement;
    animationPlace: HTMLElement;
    loader: HTMLElement;
    content: HTMLElement;
    radarLine: HTMLElement;
    mirrorBlick: HTMLElement;
    isPreloadEnded = false;

    constructor() {
        super();
        this.root = this.attachShadow({ mode: "open" });
        this.root.innerHTML = template(this.cssPath);
        this.contentPercent = this.root.querySelector(".percent") as HTMLElement;
        this.contentStage = this.root.querySelector(".stage") as HTMLElement;
        this.animationPlace = this.root.querySelector(".animation-place") as HTMLElement;
        this.loader = this.root.querySelector(".loader") as HTMLElement;
        this.content = this.root.querySelector(".content-preload") as HTMLElement;
        this.radarLine = this.root.querySelector(".radar-line") as HTMLElement;
        this.mirrorBlick = this.root.querySelector(".mirror-blick") as HTMLElement;

        //LOADER
        this.loaderImage = new Image(70, 70);
        this.loaderImage.src = `${GAMESTATE.paths.image}/loader.png`;
        this.loaderImage.onload = () => {
            this.loaderImage.classList.add("loader-img");
            this.loader.prepend(this.loaderImage);
        }
        this.classList.add("screen");
    }
    connectedCallback() {

        this.mirrorBlick.style.background = `url(${GAMESTATE.paths.image}/mirror-blick.png)`;
        this.mirrorBlick.style.backgroundPosition = `center`;
        this.mirrorBlick.style.backgroundSize = `200px`;
        this.mirrorBlick.style.backgroundPosition = `5px`;

        this.content.style.background = `url(${GAMESTATE.paths.image}/radar.jpg)`;
        this.content.style.backgroundPosition = `center`;

        this.radarLine.style.background = `url(${GAMESTATE.paths.image}/radar-line.png)`;
        this.radarLine.style.backgroundPosition = `center`;
    }
    update(data: { stage: string, percent: number }) {
        if (this.isPreloadEnded) return;
        this.percent += data.percent;
        this.stage = data.stage;

        if (this.percent >= 100) {
            this.isPreloadEnded = true;
            return;
        }
        this.contentStage.innerText = `${this.stage}`;
        this.contentPercent.innerText = `${this.percent}%`;
    }
}
customElements.define(`bl-preloader-screen`, PreloaderScreen);

function template(cssPath: string) {
    return `
    <link rel="stylesheet" type="text/css" href="${cssPath}"/>
    <div class="border rounded">
    <div class="content-preload rounded">
        <div class="mask rounded"></div>
        <div class="mirror-blick rounded"></div>
        <div class="radar-line rounded"></div>
        <div class="animation-place rounded">
            <div class="loader">
                <div class="particles">
                    <span style="--i:1;"></span>
                    <span style="--i:2;"></span>
                    <span style="--i:3;"></span>
                    <span style="--i:4;"></span>
                    <span style="--i:5;"></span>
                    <span style="--i:6;"></span>
                    <span style="--i:7;"></span>
                    <span style="--i:8;"></span>
                    <span style="--i:9;"></span>
                    <span style="--i:10;"></span>
                </div>
            </div>
        </div>

        
        <span class="percent display">0%</span>
        <span class="stage display">stage</span>
    </div>
    </div>
    `
}