import { Scene, Engine } from "../proxiBabylon";

export function runGameLoop(scene: Scene) {
    var engine = scene.getEngine();
    if (scene && engine) {
        scene.getEngine().runRenderLoop(() => {
            scene.render();
        });
    }
}
export function stopGameLoop(engine: Engine) {
    if (engine) {
        engine.stopRenderLoop();
    }
}

