import { GAMEEVENTS } from "../../GAME/Events/eventDefinition";
import { GAMESTATE } from "../../GAME/state";

export class ResultScreen extends HTMLElement {
    root: ShadowRoot;
    cssPath = `${GAMESTATE.paths.css}/resultScreen/style.css`;
    startButton: HTMLButtonElement;

    constructor() {
        super();
        this.root = this.attachShadow({ mode: "open" });
        this.root.innerHTML = template(this.cssPath);
        this.startButton = this.root.querySelector(".start-button") as HTMLButtonElement;

        this.classList.add("hide");
        this.classList.add("screen");
    }
    connectedCallback() {
        this.startButton.addEventListener("click", () => {
            dispatchEvent(new CustomEvent(GAMEEVENTS.start));
        });
    }
}
customElements.define("bl-result-screen", ResultScreen);

function template(cssPath: string) {
    return `
        <link rel="stylesheet" type="text/css" href="${cssPath}"/>
        <div class="content">
            <h1>RESULT</h1>
            <button class="start-button">S</button>
        </div>
    `;
}