import { Scene, Engine, UniversalCamera, HavokPlugin, Vector3, TransformNode } from "../proxiBabylon";
import { ScreenManager } from "../UI/screenManager/screenManager"
import { GAMESTATUS, tEnemy, tLevel } from "./types";

export var GAMESTATE = {
    paths: {
        assets: `./assets`,
        css: `./assets/css`,
        icons: `./assets/icon`,
        image: `./assets/images`,
        texture: `./assets/textures`,
        scripts: `./assets/scripts`,
        models: `./assets/models`,
    },
    state: {
        state: GAMESTATUS.START,
        stopRender: false,
        scene: null as Scene | null,
        engine: null as Engine | null,
        camera: null as UniversalCamera | null,
        canvas: null as HTMLCanvasElement | null,
        isMobile: false,
    },
    navigationMap: new Map<string, any>(),
    level: null as tLevel | null,
    physics: {
        havok: null as HavokPlugin | null,
    },
    objects: {
        enemy: new Map<string, tEnemy>(),
    }
}
export var screenManager = new ScreenManager();
//------------------------------------

export var addEnemyToState = (enemy: tEnemy) => GAMESTATE.objects.enemy.set(enemy.id, enemy);
export var getEnemyFromState = (id: string) => GAMESTATE.objects.enemy.get(id);
export var clearAllEnemyFromState = () => {
    var enemyMap = GAMESTATE.objects.enemy;
    enemyMap.forEach((v) => {
        v.dispose();
    });
    enemyMap.clear();
}