import { AssetContainer, AssetsManager, LoadAssetContainerAsync, Mesh, Scene } from "../../proxiBabylon";
import { GAMESTATE } from "../state";
import { MODELTYPE } from "../types";


export var assetsMap = new Map<MODELTYPE, AssetContainer>();
export var assetManagerEnv = null as AssetsManager | null;

export async function loadModeltoAssets(path: string, type: MODELTYPE, scene: Scene) {
    var asset = await LoadAssetContainerAsync(path, scene);
    assetsMap.set(type, asset);
}
export function getInstanceModel(type: MODELTYPE) {
    var asset = assetsMap.get(type);
    return asset ? asset.instantiateModelsToScene(name => name, false, { doNotInstantiate: true }) : null;
}

export async function initAssets(scene: Scene) {
    await loadModeltoAssets(`${GAMESTATE.paths.models}/jamPole.glb`, MODELTYPE.PLANE_0, scene)
}
export async function selectorAsset(heroType: MODELTYPE) {
    var asset = getInstanceModel(heroType);
    var root = asset?.rootNodes[0] as Mesh;
    if (root) {
        return root;
    }
    return null;
}