import { ActionManager, Animation, Axis, CreateCylinder, CreateLines, ExecuteCodeAction, Matrix, Mesh, Nullable, PickingInfo, Quaternion, Scalar, SetValueAction, Space, Tools } from "@babylonjs/core";
import { Color3, CreateBox, Scene, StandardMaterial, Vector3 } from "../../proxiBabylon";
import { CastRay } from "../CastRay/castRay";
import { utils_getDistancePoint, utils_RND_float, utils_RND_string, utils_signRandom } from "../utils";
import { COLLIDE_GROUP, COLLIDE_MASK, tEnemy } from "../types";
import { GAMESTATE } from "../state";
import { TableUp } from "../GUI/table";

export function Enemy(scene: Scene) {
    var state = {
        moved: true,
        state: "LIVE",
        live: Math.floor(Math.random() * 100),
        arsenal: {
            bullets: 100
        },
        killedEnemy: new Array<string>, // массив убитых enemy ID
        killer: "", // ID убийцы 
    }
    var id = `enemy-${utils_RND_string()}`;


    var DEFAULTSPEED = parseFloat(((Math.random() * (3 - 2 + 1) + 2) / 10).toFixed(2));
    var SPEED = DEFAULTSPEED;
    var HUNTSPEED = 0.2;
    var ESCAPESPEED = 0.4;
    var STOPED = false;
    var GRAVITY = 0.5;
    var ATTACK_INTERVAL = 10;

    var DISTANCEHUNTING = 40; // прикратить охоту если дистанция больше
    var DISTANCESCAPED = 50; // прекратить убегать если дистанция бошльше

    var isBlockedRadar = false;
    var isFindFrag = false;
    var isHunting = false;
    var isEscape = false;
    //-----------------------------------------------------------
    var huntRadarMaterial = (scene.getMaterialByName("enemy-hunting-material") as StandardMaterial);
    var escapeRadarMaterial = (scene.getMaterialByName("enemy-escape-material") as StandardMaterial);
    var searchRadardMaterial = (scene.getMaterialByName("enemy-collider-material") as StandardMaterial);
    var defaultMaterial = (scene.getMaterialByName("enemy-material") as StandardMaterial);
    var mesh = CreateBox(id, { width: 1, height: 2, depth: 1 }, scene);
    mesh.name = id;
    // mesh.ellipsoid = new Vector3(1, 1, 1)
    mesh.position.y = 3;
    mesh.material = defaultMaterial
    mesh.checkCollisions = true;
    mesh.collisionGroup = COLLIDE_GROUP.ENEMY;
    mesh.collisionMask = COLLIDE_MASK.ENEMY;

    var colliderBox = CreateCylinder(`${id}-collider`, { height: 2, diameter: 20, cap: 1, enclose: false }, scene);
    colliderBox.setParent(mesh);
    colliderBox.setPositionWithLocalVector(new Vector3(0, 1.5, 0))
    colliderBox.material = searchRadardMaterial;
    colliderBox.checkCollisions = true;
    colliderBox.collisionGroup = COLLIDE_GROUP.ENEMY;
    colliderBox.collisionMask = COLLIDE_MASK.ENEMY;
    colliderBox.actionManager = new ActionManager(scene);

    var tableUp = TableUp(`${id}-table`, mesh, new Vector3(0, 5, 0), `${id}`, scene);

    //RAY CASTING------------------------------------------------
    var groundRayData = {
        hit: false,
        distance: 0,
        object: null as Mesh | null,
    }
    var viewFWDData = {
        hit: false,
        distance: 0,
        object: null as Mesh | null,
    }

    var groundCastRay = CastRay(`ground-ray-${id}`, scene, Color3.Red());
    groundCastRay.attach(mesh, { origin: new Vector3(0, -1, -0.5), direction: new Vector3(0, -1, 0), length: 5 });
    groundCastRay.show();
    groundCastRay.cast(
        (pick: Nullable<PickingInfo>) => {
            if (pick && pick.hit) {
                groundRayData.hit = true;
                groundRayData.distance = pick.distance;
                groundRayData.object = pick?.pickedMesh as Mesh;
            } else {
                groundRayData.hit = false;
                groundRayData.distance = -1;
                groundRayData.object = null;
            }
        }, [mesh.name, "ray", "hole"]
    );

    var viewFWDCastRay = CastRay(`view-fwd-ray-${id}`, scene, Color3.Red());
    viewFWDCastRay.attach(mesh, { origin: new Vector3(0, 0.5, 0.5), direction: new Vector3(0, 0, 1), length: 20 });
    viewFWDCastRay.show();
    viewFWDCastRay.cast(
        (pick: Nullable<PickingInfo>) => {
            if (pick && pick.hit) {
                viewFWDData.hit = true;
                viewFWDData.distance = pick.distance;
                viewFWDData.object = pick?.pickedMesh as Mesh;

            } else {
                viewFWDData.hit = false;
                viewFWDData.distance = -1;
                viewFWDData.object = null;
            }
        }, [mesh.name, "ray"]
    );

    //MOVE FUNCTIONS
    var death = () => {
        state.state = "DEATH" as "LIVE" | "DEATH";
        mesh.checkCollisions = false;
        isBlockedRadar = true;
        state.moved = false;
        isEscape = false;
        isHunting = false;
        groundCastRay.dispose();
        viewFWDCastRay.dispose();
        mesh.name = "DEATH";
        tableUp.deathState();
        console.log(id, "is DEATH");
    }
    var moveFwd = () => {
        if (STOPED) return;
        mesh.moveWithCollisions(mesh.forward.scale(SPEED), true);
    }
    var lookAtEnemy = (enemy: tEnemy) => {
        var dir = enemy.getPosition().subtract(mesh.getAbsolutePosition().clone()).normalize();
        var angleEnemy = -Math.atan2(dir.z, dir.x) - Math.PI / 2;
        var fullAngle = angleEnemy + Math.PI;
        mesh.rotation.y = fullAngle;
        return new Promise((res, _) => {
            return res(enemy);
        });
    }
    var defaultMoved = () => {
        tableUp.defaultState();
        colliderBox.material = searchRadardMaterial;
        isBlockedRadar = false;
        isEscape = false;
        isHunting = false;
        SPEED = parseFloat(((Math.random() * (3 - 2 + 1) + 2) / 10).toFixed(2));
    }
    var escape = (fragPosition: Vector3, hunterID: string) => {
        var hunter = GAMESTATE.objects.enemy.get(hunterID) as tEnemy;
        tableUp.escapeState();
        colliderBox.material = escapeRadarMaterial;
        isBlockedRadar = true;
        isEscape = true;
        isHunting = false;
        SPEED = ESCAPESPEED;
        STOPED = false;
        mesh.lookAt(fragPosition);
        mesh.rotation.y += Math.PI;

        var count = 10;
        var o$ = scene.onBeforeRenderObservable.add(() => {
            if (!count) {
                if (state.state === "DEATH") {
                    o$.remove();
                    death();
                }
                if (Vector3.Distance(mesh.getAbsolutePosition().clone(), hunter.getPosition()) > DISTANCESCAPED || hunter.getState().state === "DEATH") {
                    o$.remove();
                    defaultMoved();
                }
                mesh.rotation.y += 0.3 * utils_signRandom();
                count = 10
            }
            count--;
        })
    }
    var hunting = (enemy: tEnemy) => {
        tableUp.huntingState();
        colliderBox.material = huntRadarMaterial;
        isBlockedRadar = true;
        isHunting = true;
        isEscape = false;
        SPEED = HUNTSPEED;
        STOPED = false;

        var count = 5;
        var o$ = scene.onBeforeRenderObservable.add(() => {
            if (!attack(enemy)) {
                o$.remove();
                defaultMoved();
            };
            if (!count) {
                if (state.state === "DEATH") {
                    o$.remove();
                    death();
                }
                if (Vector3.Distance(mesh.getAbsolutePosition().clone(), enemy.getPosition()) > DISTANCEHUNTING || enemy.getState().state === "DEATH") {
                    o$.remove();
                    defaultMoved();
                }
                lookAtEnemy(enemy);
                count = 5;
            }
            count--;
        })
    }
    var attack = (frag: tEnemy) => {
        if (!state.arsenal.bullets) {
            return false;
        }
        ATTACK_INTERVAL -= 1;
        if (ATTACK_INTERVAL === 0) {
            ATTACK_INTERVAL = 10;
            state.arsenal.bullets -= 1;
            frag.damage(1);
        }
        return true;
    }
    var damage = (val: number = 1) => {
        if (state.live) {
            state.live -= val;
            tableUp.updateLive(state.live.toString())
        } else {
            death();
        }
        return state.state;
    }
    //----------------------

    return {
        id: id,
        mesh: mesh,
        setPosition: (val: Vector3) => mesh.position = val,
        getPosition: () => mesh.getAbsolutePosition().clone(),
        lookAt: (pt: Vector3) => {
            mesh.lookAt(pt);
        },
        lookAtEnemy: lookAtEnemy,
        moveFWD: moveFwd,
        moveTo: (to: Vector3) => {
            mesh.lookAt(to);
            mesh.moveWithCollisions(mesh.forward.scale(SPEED), true);
        },
        rotateY: (angle: number) => {
            mesh.rotation.y = angle;
        },
        jump: (endCallback: () => void) => {
            var jump = 0.3;
            var maxY = 0;
            var o$ = scene.onBeforeRenderObservable.add(() => {
                jump -= 0.06;
                mesh.position.y += jump;
                if (maxY < mesh.position.y) {
                    maxY = mesh.position.y
                }
                if (jump <= 0) {
                    // console.log("Max", maxY)
                    o$.remove();
                    endCallback();
                }
            })

        },
        dispose: () => {
            mesh.dispose();
            groundCastRay.dispose();
            viewFWDCastRay.dispose();
        },
        getState: () => state,
        attack: attack,
        damage: damage,
        rob: (enemy: tEnemy) => {
            state.arsenal.bullets += enemy.getState().arsenal.bullets;
            state.killedEnemy.push(enemy.id);
            console.log(id, state.arsenal.bullets)
        },
        getRayData: () => ({
            ground: groundRayData,
            forward: viewFWDData,
        }),
        nextTargetPt: () => {
            var pt = utils_getDistancePoint(mesh.position.clone(), GAMESTATE.navigationMap.get("ground")!, 1000);
            mesh.lookAt(pt);
        },
        toGround: () => {
            if (groundRayData.distance > GRAVITY || !groundRayData.hit) {
                mesh.moveWithCollisions(mesh.up.scale(-GRAVITY), false);
            }
        },
        getRadarCollider: () => colliderBox,
        getRadarFragIntersection: () => {
            if (isBlockedRadar) return;
            GAMESTATE.objects.enemy.forEach(enm => {
                if (enm.id !== id && Vector3.Distance(mesh.position, enm.getPosition()) < 25 && enm.getState().state === "LIVE") {

                    hunting(enm)
                    enm.escape(mesh.position.clone(), id);
                }
            });

        },
        escape: escape,
        hunting: hunting,
    }
}
export function createEnemy(position: Vector3, scene: Scene) {
    var enemy = Enemy(scene);
    enemy.setPosition(position);
    return enemy;
}
// MOVE LOGIC----------------------------------------------

export function moveEnemy(enemy: tEnemy, scene: Scene) {
    var stopedGravity = false;
    var stopedGround = false;
    var stopedFWD = false;
    var isFindPath = false;
    var isFindMost = false;
    var endEdgeCallback = () => stopedGround = false;
    var endMostCallback = () => stopedFWD = false;
    var endJumpCallback = () => stopedGravity = false;

    //---------------------------------------------------------
    var o$ = scene.onBeforeRenderObservable.add(() => {
        if (!stopedGravity) {
            enemy.toGround();
        }

        if (enemy.getState().moved) {
            if (!stopedGround) {
                enemy.moveFWD();
                enemy.getRadarFragIntersection();
                var rays = enemy.getRayData();
                if (rays.ground.hit) {
                    if (rays.ground.object.name.includes("edge")) {
                        stopedGround = true;
                        edgeMove(enemy, endEdgeCallback, scene);

                    } else if (rays.ground.object.name.includes("markMost") && !isFindMost) {
                        isFindMost = true;
                        var parseName = (rays.ground.object.name as String).split("-")
                        var mostName = parseName.pop();
                        var sideNumber = parseName.pop();
                        if (mostName && sideNumber) {
                            mostMove(mostName, parseInt(sideNumber), enemy, () => {
                                // console.log("MOst End")
                            }, scene);
                        }

                    } else if (rays.ground.object.name.includes("path") && !isFindPath) {
                        isFindPath = true;
                        // stopedGround = true;
                        moveByPath(rays.ground.object.name, enemy, () => isFindPath = false, scene);
                    }
                }
            }
            if (!stopedFWD) {
                var rays = enemy.getRayData();
                if (rays.forward.hit) {
                    if (rays.forward.object.name.includes("hole")) {

                        enemy.jump(() => {
                            // console.log("jump")
                        })
                    }
                }
            }
        }


    });
}
export function edgeMove(enemy: tEnemy, endCallback: () => void, scene: Scene) {
    enemy.nextTargetPt();
    endCallback();
}
export function mostMove(mostName: string, sideNumber: number, enemy: tEnemy, endCallback: () => void, scene: Scene) {
    var points = GAMESTATE.navigationMap.get("mosts").get(mostName) as Array<Vector3>;

    var targetPoint = points[sideNumber];
    var endPoint = (sideNumber === 0) ? points[1] : points[0];
    if (points) {
        enemy.lookAt(targetPoint);
        var o$ = scene.onBeforeRenderObservable.add(() => {
            if (Vector3.Distance(targetPoint, enemy.getPosition()) < 3) {
                enemy.lookAt(endPoint);
                endCallback();
                o$.remove();
            }
        });
    }
}
export function moveByPath(pathName: string, enemy: tEnemy, endCallback: () => void, scene: Scene) {
    var pathNavPt = GAMESTATE.navigationMap.get("paths").get(pathName) as Array<Vector3>;
    var selectAction = true; //randomSelectAction(5);
    if (selectAction) {
        var { targetPoint, partPathPt } = getTargetPoint(pathNavPt, enemy.getPosition());
        var curve = drawCurve(partPathPt, scene);

        targetPoint = new Vector3(partPathPt[0].x, enemy.getPosition().y, partPathPt[0].z)
        enemy.lookAt(targetPoint);
        var maxPartIndex = partPathPt.length - 1;
        var index = 0;
        var o$ = scene.onBeforeRenderObservable.add(() => {
            if (Vector3.Distance(targetPoint!, enemy.getPosition()) < 5) {
                index += 1;
                if (index >= maxPartIndex) {
                    endCallback();
                    // console.log("End Path")
                    curve.dispose();
                    o$.remove();
                }
                targetPoint = partPathPt[index];
                enemy.lookAt(targetPoint);
            }
        });

    } else {
        endCallback();
        console.log("end path");
    }
}
//CUBE RANDOM
var randomSelectAction = (edge: number) => (Math.random() * 10 > edge) ? false : true;

//UTILS FOR PATH
var getTargetPoint = (pathPoints: Array<Vector3>, enemyPosition: Vector3) => {
    var minDistance = 1000000;
    var targetIndex = 0;
    var targetPoint = null as Vector3 | null;
    var maxIndex = pathPoints.length;

    pathPoints.forEach((pt, i) => {
        var distance = Vector3.Distance(pt, enemyPosition);
        if (distance < minDistance) {
            minDistance = distance;
            targetPoint = pt;
            targetIndex = i;
        }
    });
    var direction = 1;
    if (targetIndex > maxIndex / 2) {
        direction = -1;
    }
    var partPathPt = direction < 0 ? pathPoints.slice(0, targetIndex).reverse() : pathPoints.slice(targetIndex, maxIndex);
    return { targetPoint, partPathPt };
}
var drawCurve = (points: Array<Vector3>, scene: Scene) => {
    var drawCurve = CreateLines(`lns-${utils_RND_string()}`, { points: points }, scene);
    return {
        dispose: () => {
            drawCurve.dispose();
        }
    }
}
