import { Scene, Vector3 } from "../../proxiBabylon";
import { PreloaderScreen } from "../../UI/screensExport";
import { createEnemy, moveEnemy } from "../Enemy/enemy";
import { runGameLogicCircle, runGameLoop } from "../gameCircle";
import { addEnemyToState, GAMESTATE, getEnemyFromState, screenManager } from "../state";
import { GAMESTATUS, SCREENS, tLevel } from "../types";
import { utils_RND_Clamp_integer, utils_signRandom } from "../utils";
import { GAMEEVENTS } from "./eventDefinition";

//GAME EVENTS
(() => {
    addEventListener(GAMEEVENTS.preload, (e) => {
        screenManager.switchState(GAMESTATUS.PRELOAD);
        var preloadScreen = (screenManager!.getScreen(SCREENS.preload) as PreloaderScreen);
        preloadScreen.update((e as CustomEvent).detail);
    });
    addEventListener(GAMEEVENTS.start, (e) => {
        GAMESTATE.state.state = GAMESTATUS.START;
        screenManager.switchState(GAMESTATUS.START);
    });
    addEventListener(GAMEEVENTS.runGame, (e) => {
        if (GAMESTATE.state.isMobile) {
            var root = document.querySelector("#root") as HTMLCanvasElement;
            if (root.requestFullscreen) {
                root.requestFullscreen();
                console.log("full")
            } else {
                console.log("no-full")
            }
            // 
        }
        var scene = (e as CustomEvent).detail.scene as Scene;
        if (scene) {
            screenManager.switchState(GAMESTATUS.RUN);
            (GAMESTATE.level as tLevel).run();
            runGameLogicCircle();
            runGameLoop(scene);
            GAMESTATE.state.state = GAMESTATUS.RUN;
        }
    });
    addEventListener(GAMEEVENTS.pauseLevel, () => {
        var level = GAMESTATE.level as tLevel;
        if (level.state()) {
            level.stop();
        } else {
            level.run();
        }

    });
    addEventListener(GAMEEVENTS.stopLevel, () => {
        (GAMESTATE.level as tLevel).stop();
        dispatchEvent(new CustomEvent(GAMEEVENTS.start));
    });
    addEventListener(GAMEEVENTS.endLevel, () => {
        console.log("Level End")
        dispatchEvent(new CustomEvent(GAMEEVENTS.start));
    });
    addEventListener(GAMEEVENTS.endGame, () => {
        console.log("End GAme")
        dispatchEvent(new CustomEvent(GAMEEVENTS.start));
    });
    //ENEMY
    addEventListener(GAMEEVENTS.createEnemy, async (e) => {
        var enemy = createEnemy(new Vector3(
            utils_RND_Clamp_integer(10, 30) * utils_signRandom(),
            1,
            utils_RND_Clamp_integer(10, 30) * utils_signRandom()), GAMESTATE.state.scene!);
        moveEnemy(enemy, GAMESTATE.state.scene!)
        addEnemyToState(enemy);
    });
    addEventListener(GAMEEVENTS.attackEnemy, (e) => {
        var data = (e as CustomEvent).detail;
        var assaulterID = data.assaulter; //атакующий
        var victimID = data.victim; //жертва
        if (assaulterID && victimID) {
            var assaulter = getEnemyFromState(assaulterID);
            var victim = getEnemyFromState(victimID);
            if (assaulter && victim) {
                var state = victim.damage(5);
                assaulter.attack();
                if (state === "DEATH") {
                    assaulter.rob(victim);
                    victim.getState().killer = assaulter.id;
                }
            }

        }
    })
})();

//SYSTEM EVENTS
(() => {
    document.body.addEventListener("contextmenu", (e) => {
        if (e.button === 2) {
            e.preventDefault();
            return false;
        }
    });
    document.addEventListener("fullscreenchange", () => {

    })
})();