import { interval, takeWhile } from "rxjs";
import {
    Scene, UniversalCamera, HemisphericLight,
    Vector3, Color3, Color4,
} from "../proxiBabylon";
import { MobileCotrol } from "./Controls/mobileControl/mobileControl";
import { GAMESTATE } from "./state";
import { COLLIDE_GROUP, COLLIDE_MASK, MODELTYPE, tHero } from "./types";
import { Hero, keyController } from "./Hero/hero";
import { DirectionalLight, ShadowGenerator, StandardMaterial } from "@babylonjs/core";
import { KeyboardControl } from "./Controls/KeyControl/keyboardControl";
import { selectorAsset } from "./Loaders/loaderModels";
import { EnemyGenerator } from "./Enemy/enemyGenerator";
import { createEnemy } from "./Enemy/enemy";

export async function createEnvironment(scene: Scene) {
    var isMobile = GAMESTATE.state.isMobile;
    if (isMobile) {
        var mobileControl = MobileCotrol(scene);
        scene.clearColor = new Color4(70 / 255, 70 / 255, 70 / 255, 1);
    } else {
        var keyBoardControl = KeyboardControl(scene);
        scene.clearColor = new Color4(40 / 255, 41 / 255, 40 / 255, 1);
    }


    scene.ambientColor = new Color3(4 / 255, 9 / 255, 12 / 255);
    scene.collisionsEnabled = true;

    var baseCamera = new UniversalCamera("base-camera", new Vector3(0, 300, 0), scene);
    // baseCamera.fov = 1
    baseCamera.target = new Vector3(0, 1, 0);
    baseCamera.maxZ = 1050;
    baseCamera.attachControl();

    var hlight = new HemisphericLight("base-light", new Vector3(0, 1, 0), scene);
    hlight.intensity = 1.9;
    var dirLight = new DirectionalLight("shagow-light", new Vector3(-0.3, -1, -0.5), scene);

    var shadowGen = new ShadowGenerator(1024, dirLight);
    //---------------------------------------------------------------------
    // Ground(scene);
    var enemyMaterial = new StandardMaterial("enemy-material", scene);
    enemyMaterial.diffuseColor = new Color3(0.3, 0.3, 0.7);
    var enemyEscapeMaterial = new StandardMaterial("enemy-escape-material", scene);
    enemyEscapeMaterial.diffuseColor = new Color3(0.9, 0.3, 0.3);
    enemyEscapeMaterial.alpha = 0.6;
    var enemyHuntingMaterial = new StandardMaterial("enemy-hunting-material", scene);
    enemyHuntingMaterial.diffuseColor = new Color3(0.1, 0.3, 0.9);
    enemyHuntingMaterial.alpha = 0.6;
    var enemyColliderMaterial = new StandardMaterial("enemy-collider-material", scene);
    enemyColliderMaterial.diffuseColor = new Color3(0.3, 0.3, 0.7);
    enemyColliderMaterial.alpha = 0.6;

    var pole = await selectorAsset(MODELTYPE.PLANE_0);


    var paths = new Map<string, Array<Vector3>>();// разметка путей
    var mosts = new Map<string, Array<Vector3>>();// разметка мостов


    if (pole) {
        pole.name = "pole";
        pole.getChildMeshes().forEach(m => {
            m.checkCollisions = true;
            shadowGen.addShadowCaster(m);
            m.receiveShadows = true;
            if (m.name.includes("ground")) {
                m.receiveShadows = true;
                m.collisionGroup = COLLIDE_GROUP.GROUND;
                m.collisionMask = COLLIDE_GROUP.GROUND;
            } else {
                m.visibility = 0;
            }

            if (m.name.includes("most")) {
                var marksPoint = m.getChildTransformNodes(false, (n) => n.name.includes("pt")).map(pt => pt.getAbsolutePosition());
                mosts.set(m.name, marksPoint)
                m.collisionGroup = COLLIDE_GROUP.GROUND;
                m.collisionMask = COLLIDE_MASK.GROUND;

            }
            if (m.name.includes("path")) {
                var marksPoint = m.getChildTransformNodes(false, (n) => n.name.includes("pt")).map(pt => pt.getAbsolutePosition());
                paths.set(m.name, marksPoint)
                m.collisionGroup = COLLIDE_GROUP.MARK;
                m.collisionMask = COLLIDE_MASK.MARK;
            }
            if (m.name.includes("hole")) {
                m.checkCollisions = false;
                m.name = "hole"
                m.collisionGroup = COLLIDE_GROUP.MARK;
                m.collisionMask = COLLIDE_MASK.MARK;
            }
        });
        GAMESTATE.navigationMap.set("paths", paths);
        GAMESTATE.navigationMap.set("mosts", mosts);

        var polePoints = pole.getChildTransformNodes(false, (pt) => pt.name.includes("pt-nav")).map((node) => node.getAbsolutePosition().clone());
        GAMESTATE.navigationMap.set("ground", polePoints);


        // var nav1 = utils_getNavigationPoints(outNavPoints, 6000);
        // var nav2 = utils_getNavigationPoints(outNavPoints, 6000);
        // var nav3 = utils_getNavigationPoints(outNavPoints, 6000);


        // var navigator1 = await CrowdNavigation(pole.getChildMeshes(), nav1.pt1, nav1.pt2, scene);
        // navigator1.drawPath();
        // var navigator2 = await CrowdNavigation(pole.getChildMeshes(), nav2.pt1, nav2.pt2, scene);
        // navigator2.drawPath();
        // var navigator3 = await CrowdNavigation(pole.getChildMeshes(), nav3.pt1, nav3.pt2, scene);
        // navigator3.drawPath();



        // navigator1.addAgent(enemy1);
        // navigator1.run();
        //navigator1.goAgent(enemy1.getCrowdIndex(), outNavPoints[10])
    }

    var hero = await Hero(MODELTYPE.EMPTY, scene);


    var enemyGenerator = EnemyGenerator(100);
    enemyGenerator.run();

    // var enemy = createEnemy(new Vector3(30, 1, 30), scene);
    // var enemy2 = createEnemy(new Vector3(-12, 1, 10), scene);

    // setTimeout(() => {
    //     enemy.lookAtEnemy(enemy2);
    //     enemy2.lookAtEnemy(enemy);
    // }, 2000)

    //ENEMY LOGIC------------------------------

    if (hero) {
        function fnc() {
            if (isMobile) {
                var data = mobileControl.data();
                var angle = -Math.atan2(data.y, data.x)
                // console.log(Tools.ToDegrees(angle))
                hero.rotateAngl(angle);
            } else {
                var kbdata = keyBoardControl.getState();
                keyController(hero, kbdata);
                // console.log(kbdata);
            }

        }

        scene.onBeforeRenderObservable.add(() => {
            fnc();
        })
    }
    setTimeout(console.log, 1000, "hello")

}