import { interval, takeWhile } from "rxjs";
import { GAMEEVENTS } from "../Events/eventDefinition";

export function EnemyGenerator(max: number) {
    var i = 0
    var g$ = interval(1000).pipe(takeWhile(() => i < max));
    var run = () => {
        g$.subscribe({
            next: () => {
                i += 1;
                dispatchEvent(new CustomEvent(GAMEEVENTS.createEnemy))
            },
            complete: () => {
                // console.log("Enemy Generator Ended")
            }
        })
    }
    return {
        run: run,
    }
}