import { GAMESTATE } from "../../GAME/state";
import { GAMESTATUS, SCREENS } from "../../GAME/types";

export class ScreenManager {
    screens = new Map<SCREENS, HTMLElement>();
    width = window.innerWidth;
    height = window.innerWidth;

    constructor() {

    }
    switchState(state: GAMESTATUS) {
        switch (state) {
            case GAMESTATUS.PRELOAD: {
                this.hide(SCREENS.levelResult);
                this.hide(SCREENS.endGame);
                this.hide(SCREENS.game);
                this.hide(SCREENS.level);
                this.hide(SCREENS.levelMobile);
                this.hide(SCREENS.start);
                this.show(SCREENS.preload);
                break;
            }
            case GAMESTATUS.START: {
                this.hide(SCREENS.levelResult);
                this.hide(SCREENS.endGame);
                this.hide(SCREENS.game);
                this.hide(SCREENS.level);
                this.hide(SCREENS.levelMobile);
                this.hide(SCREENS.preload);
                this.show(SCREENS.start);
                break;
            }
            case GAMESTATUS.RUN: {
                this.hide(SCREENS.levelResult);
                this.hide(SCREENS.endGame);
                this.hide(SCREENS.start);
                this.hide(SCREENS.preload);
                this.show(SCREENS.game);
                if (!GAMESTATE.state.isMobile) {
                    this.show(SCREENS.level);
                } else {
                    this.show(SCREENS.levelMobile);
                }

                break;
            }
            case GAMESTATUS.PAUSE: {
                break;
            }
            case GAMESTATUS.LEVELEND: {
                this.hide(SCREENS.start);
                this.hide(SCREENS.endGame);
                this.hide(SCREENS.preload);
                this.hide(SCREENS.game);
                this.hide(SCREENS.level);
                this.hide(SCREENS.levelMobile);
                this.show(SCREENS.levelResult);
                break;
            }
            case GAMESTATUS.END: {
                this.hide(SCREENS.start);
                this.hide(SCREENS.preload);
                this.hide(SCREENS.game);
                this.hide(SCREENS.level);
                this.hide(SCREENS.levelMobile);
                this.hide(SCREENS.levelResult);
                this.show(SCREENS.endGame);
                break;
            }
        }
    }
    getScreen(id: SCREENS) {
        return this.screens.get(id);
    }
    add(id: SCREENS, screen: HTMLElement) {
        this.screens.set(id, screen);
        return screen;
    }
    hide(id: SCREENS) {
        (this.screens.get(id) as HTMLElement).classList.add("hide");
    }
    show(id: SCREENS) {
        (this.screens.get(id) as HTMLElement).classList.remove("hide");
    }
    resize() {
        var widthScale = window.innerWidth / this.width;
        if (this.width != window.innerWidth) {
            this.width = innerWidth;
        }
        this.screens.forEach(s => {
            s.style.scale = `scale(${widthScale})`;
        })
    }
    //UPDATE FUNCTIONS
    updateLevelScreen(data: any) {
        console.log("level:", data.title)
    }
}
