
export var GAMEEVENTS = {
    preload: "preload",
    preloadUpdate: "preload-update",
    start: "start",
    runGame: "run-game",
    pauseLevel: "pause-level",
    stopLevel: "stop-level",
    endLevel: "end-level",
    endGame: "end-game",
    createEnemy: "create-enemy",
    disposeEnemy: "dispose-enemy",
    attackEnemy: "attack-enemy",
}

export async function preload() {
    return new Promise<boolean>(res => {
        var tm = setTimeout(() => {
            clearTimeout(tm);
            dispatchEvent(new CustomEvent(GAMEEVENTS.preload, { detail: { percent: 25, stage: "loader core" } }));
            res(true);
        }, 200);
    });
}