import { FollowCamera, Nullable, PickingInfo } from "@babylonjs/core";
import { Vector3, StandardMaterial, Scene, Color3, CreateBox, Mesh, Axis } from "../../proxiBabylon";
import { CastRay } from "../CastRay/castRay";
import { COLLIDE_GROUP, COLLIDE_MASK, MODELTYPE } from "../types";

export type HeroState = {
    mesh: Mesh,
    stop: () => void,
    rotateAngl: (angle: number) => void,
    rotateCW: () => void,
    rotateCCW: () => void,
    fwd: () => void,
    bwd: () => void,
    jump: () => void,
}

export async function Hero(heroType: MODELTYPE, scene: Scene) {
    var FWD_SPEED = 0.2;
    var JUMP_POWER = 0.5;
    var GRAVITY = 0.05;
    var jumped = false;
    var currentSpeed = 0;

    var mesh = CreateBox("hero", { width: 1, height: 2, depth: 1 }, scene);
    mesh.position = new Vector3(10, 1, 0);
    var material = new StandardMaterial("hero-material", scene);
    material.diffuseColor = new Color3(0.4, 0.7, 0.8);
    mesh.material = material;
    mesh.checkCollisions = true;
    mesh.collisionGroup = COLLIDE_GROUP.HERO;
    mesh.collisionMask = COLLIDE_MASK.HERO;

    //RAY CASTING-------------------------------------------

    var isGroundRay = false;
    var groundHeight = 0;

    var groundCastRay = CastRay("ground-ray", scene, Color3.Red());
    groundCastRay.attach(mesh, { origin: new Vector3(0, -1, -0.5), direction: new Vector3(0, -1, 0), length: 1 });
    groundCastRay.show();
    groundCastRay.cast(
        (pick: Nullable<PickingInfo>) => {
            if (pick && pick.hit) {
                isGroundRay = true;
                var distance = pick.distance;
                groundHeight = distance;
            } else {
                isGroundRay = false;
            }
        }, [mesh.name, "ray"]
    );
    var forwardCastRay = CastRay("forward-ray", scene, Color3.Red());
    forwardCastRay.attach(mesh, { origin: new Vector3(0, 0.5, -0.5), direction: mesh.forward.clone(), length: 10 });
    forwardCastRay.show();
    forwardCastRay.cast(
        (pick: Nullable<PickingInfo>) => {
            if (pick && pick.hit) {

            } else {

            }
        }, [mesh.name, "ray"]
    );

    //LOGIC LOOP ----------------------------
    var i = 10;
    scene.onBeforeRenderObservable.add(() => {
        // if (i > 0) {
        //     i -= 1;
        // } else {
        //     console.log("ground ray", isGroundRay, groundHeight);
        //     i = 10;
        // }
        if (groundHeight > 0.05) {
            mesh.moveWithCollisions(mesh.up.scale(-0.2));
        } else {
            // var tm = setTimeout(() => {
            //     jumped = false;
            //     clearTimeout(tm);
            // }, 1000);
            jumped = false;
        }

    })

    var addCamera = () => {
        var camera = new FollowCamera("hero-follow-camera", new Vector3(0, 10, 10), scene, mesh);
        camera.radius = 10;
        camera.heightOffset = 3;
        camera.rotationOffset = 0;
        camera.maxCameraSpeed = 2;
        camera.cameraAcceleration = 0.05;
        // camera.minZ = 6;

        // camera.attachControl(true);
        scene.setActiveCameraByName(camera.name);

    }
    // addCamera();

    return {
        mesh: mesh,
        stop: () => {
            currentSpeed = 0;
        },
        rotateAngl: (angle: number) => {
            mesh.rotation.y = angle
        },
        rotateCW: () => {
            mesh.moveWithCollisions(mesh.forward.scale(0.01));
            mesh.rotate(Axis.Y, 0.1);
        },
        rotateCCW: () => {
            mesh.moveWithCollisions(mesh.forward.scale(0.01));
            mesh.rotate(Axis.Y, -0.1);
        },
        fwd: () => {
            if (currentSpeed < FWD_SPEED) {
                currentSpeed += 0.025;
            }
            mesh.moveWithCollisions(mesh.forward.scale(currentSpeed));
        },
        bwd: () => {
            if (currentSpeed < FWD_SPEED * 0.5) {
                currentSpeed += 0.015;
            }
            mesh.moveWithCollisions(mesh.forward.scale(currentSpeed));
        },
        jump: () => {
            if (!jumped) {
                console.log("JUMP", jumped)
                jumped = true;
                var jump_power = JUMP_POWER;

                var o$ = scene.onBeforeRenderObservable.add(() => {
                    if (currentSpeed > 0.025) {
                        currentSpeed -= 0.002;
                    }
                    if (jump_power > GRAVITY) {
                        jump_power -= GRAVITY * 0.5;
                        mesh.movePOV(0, jump_power, currentSpeed)
                    } else {
                        o$.remove();
                    }

                });
            }
        }
    }
}
//-----------------------------------------------
export function keyController(heroState: HeroState, state: number) {
    switch (state) {
        case 0: {//stop
            heroState.stop();
            break;
        }
        case 1: { // fwd  
            heroState.fwd();
            break;
        }
        case 2: { // back            
            heroState.bwd();
            break;
        }
        case 8: { // rotate cw
            heroState.rotateCW();
            break;
        }
        case 4: { // rotate ccw
            heroState.rotateCCW();
            break;
        }
        case 5: { //fwd + ccw
            heroState.rotateCCW();
            break;
        }
        case 9: { //fwd + cw
            heroState.rotateCW();
            break;
        }
        case 6: { //bwd + cw

            break;
        }
        case 10: { //bwd + ccw

            break;
        }
        case 16: {
            heroState.jump();
            break;
        }
        case 17: {
            heroState.jump();
            break;
        }
        case 32: {
            console.log("Shift Key")
            break;
        }
    }
}