import { ActionManager, ExecuteCodeAction, Scene } from "@babylonjs/core";

export function KeyboardControl(scene: Scene) {
    var state = 0b000000; // 0-> forward 1->back 2->cwRot 3->ccwRot 4->Space 5->Shift

    scene.actionManager = new ActionManager(scene);
    scene.actionManager.registerAction(new ExecuteCodeAction(
        ActionManager.OnKeyDownTrigger,
        (e) => {
            var code = e.sourceEvent.code;
            if (code === "KeyA") { // CW
                // console.log("A", state)
                state = setBit(state, 2)
            }
            if (code === "KeyD") { //CCW
                // console.log("D", state)
                state = setBit(state, 3)
            }
            if (code === "KeyW") { //FWD
                // console.log("W", state)
                state = setBit(state, 0)
            }
            if (code === "KeyS") { //BWD
                // console.log("S", state)
                state = setBit(state, 1)
            }
            if (code === "Space") { //jump 16
                // console.log("Space", state)
                state = setBit(state, 4)
            }
            if (code === "ShiftLeft" || code === "ShiftRight") { // 32
                // console.log("Shift", state)
                state = setBit(state, 5)
            }
        }
    ));
    scene.actionManager.registerAction(new ExecuteCodeAction(
        ActionManager.OnKeyUpTrigger,
        (e) => {
            var code = e.sourceEvent.code;
            if (code === "KeyA") {
                state = clearBit(state, 2)
            }
            if (code === "KeyD") {
                state = clearBit(state, 3)
            }
            if (code === "KeyW") {
                state = clearBit(state, 0)
            }
            if (code === "KeyS") {
                state = clearBit(state, 1)
            }
            if (code === "Space") {
                state = clearBit(state, 4)
            }
            if (code === "ShiftLeft" || code === "ShiftRight") {
                state = clearBit(state, 5)
            }
        }));
    return {
        getState: () => state,
    }
}

var setBit = (num: number, bit: number) => num | 1 << bit;
var clearBit = (num: number, bit: number) => num & ~(1 << bit);