import { AdvancedDynamicTexture, Rectangle, TextBlock } from "@babylonjs/gui";
import { CreatePlane, Mesh, Scene, Vector3 } from "../../proxiBabylon";

export function TableUp(name: string, parent: Mesh, localPos: Vector3, content: string, scene: Scene) {
    var mesh = CreatePlane(name, { width: 20, height: 10 }, scene);
    mesh.setParent(parent);
    mesh.setPositionWithLocalVector(localPos);
    mesh.billboardMode = Mesh.BILLBOARDMODE_ALL;
    var gui = AdvancedDynamicTexture.CreateForMesh(mesh, 1024, 512);
    // gui.rootContainer.scaleX = window.devicePixelRatio;
    // gui.rootContainer.scaleY = window.devicePixelRatio;


    var rect2 = new Rectangle();
    rect2.width = 1024;
    rect2.height = 512;
    rect2.background = "transparent";
    rect2.color = "#fff";
    gui.addControl(rect2);

    var tb = new TextBlock();
    tb.text = content;
    tb.color = "#000";
    tb.fontSize = 250;
    // tb.fontWeight = "bold";
    rect2.addControl(tb)

    return {
        huntingState: () => {
            rect2.background = "#f66";
        },
        escapeState: () => {
            rect2.background = "#66f";
        },
        defaultState: () => {
            rect2.background = "transparent";
        },
        deathState: () => {
            rect2.background = "#333";
        },
        updateLive: (val: string) => {
            tb.text = val;
        }
    }

}