import { GAMEEVENTS } from "../../GAME/Events/eventDefinition";
import { GAMESTATE } from "../../GAME/state";

export class LevelScreen extends HTMLElement {
    root: ShadowRoot;
    cssPath = `${GAMESTATE.paths.css}/levelScreen/style.css`;
    pauseButton: HTMLButtonElement;
    stopButton: HTMLButtonElement;

    constructor() {
        super();
        this.root = this.attachShadow({ mode: "open" });
        this.root.innerHTML = template(this.cssPath);
        this.pauseButton = this.root.querySelector(".pause-button") as HTMLButtonElement;
        this.stopButton = this.root.querySelector(".stop-button") as HTMLButtonElement;
        this.classList.add("screen");
    }
    connectedCallback() {
        this.pauseButton.addEventListener("click", () => {
            dispatchEvent(new CustomEvent(GAMEEVENTS.pauseLevel));
        });
        this.stopButton.addEventListener("click", () => {
            dispatchEvent(new CustomEvent(GAMEEVENTS.stopLevel));
        });
    }
}
customElements.define("bl-level-screen", LevelScreen);

function template(cssPath: string) {
    return `
        <link rel="stylesheet" type="text/css" href="${cssPath}"/>
        <div class="content">            
            <div class="top-content side"></div>
            <div class="center-content side">
                <div class="center-right-content side">
                    <button class="pause-button">||</button>
                    <button class="stop-button">S</button>
                </div>                
                <div class="center-center-content side"></div>
                <div class="center-left-content side"></div>
            </div>
           <div class="bottom-content side"></div>
        </div>
    `;
}