import { Color3, Mesh, Nullable, Observer, PickingInfo, Ray, RayHelper, Scene, Vector3 } from "@babylonjs/core";

export function CastRay(id: string, scene: Scene, color?: Color3) {
    var id = id;
    var rayColor = color ?? Color3.White();
    var ray = new Ray(Vector3.Zero(), Vector3.Zero(), 0, 0.);
    var rayHelper = new RayHelper(ray);
    var cast$: Observer<Scene> | null = null;
    return {
        id: id,
        ray: () => ray,
        attach: (mesh: Mesh, options: { origin: Vector3, direction: Vector3, length: number }) => {
            rayHelper.attachToMesh(mesh, options.direction, options.origin, options.length);
        },
        show: () => rayHelper.show(scene, rayColor),
        hide: () => rayHelper.hide(),
        cast: (workerFnc: (pick: Nullable<PickingInfo>) => void, nonCollideMeshesName: Array<string>) => {
            cast$ = scene.onBeforeRenderObservable.add(() => {
                var pick = scene.pickWithRay(ray, (mesh) => {
                    var nonCollide = true;
                    nonCollideMeshesName.forEach(name => {
                        if (mesh.name === name) nonCollide = false;
                    })
                    return nonCollide;
                });
                workerFnc(pick);
            });
        },
        dispose: () => {
            rayHelper.dispose();
            if (cast$) {
                cast$.remove(true);
            }
        }
    }
}