import { Scene, Engine, Vector3 } from "../proxiBabylon";
import { initHavok } from "./Havok/initHavok";
import("@babylonjs/core/Physics/physicsEngineComponent")

export async function InitGame(canvas: HTMLCanvasElement) {

    var engine = initEngine(canvas);
    var scene = initScene(engine);
    var havok = await initHavok();
    if (scene && engine && havok) {
        scene.enablePhysics(new Vector3(0, -9.81, 0), havok);
        addEventListener("resize", () => {
            engine!.resize();
        });
        return { scene, engine, havok };
    }
    return null;
}

const initEngine = (canvas: HTMLCanvasElement | null): Engine | null => {
    if (canvas) {
        return new Engine(canvas, true, { preserveDrawingBuffer: false, stencil: true }, true);
    }
    return null
}
const initScene = (engine: Engine | null): Scene | null => {
    if (engine) {
        return new Scene(engine);
    }
    return null;
}